<template>
  <div>
    <!-- Header 区域 -->
    <van-nav-bar fixed title="文章详情" left-arrow @click-left="$router.back()" />
    <!-- 文章加载状态 -->
    <van-loading class="loading" color="#1989fa" v-if="Object.keys(articleDetailList).length === 0">加载中</van-loading>
    <!-- 文章信息区域 -->
    <div v-else>
      <div class="article-container">
        <!-- 文章标题 -->
        <h1 class="art-title">{{ articleDetailList.title }}</h1>

        <!-- 用户信息 -->
        <van-cell center :title="articleDetailList.aut_name" :label="articleDetailList.pubdate">
          <template #icon>
            <img :src="articleDetailList.aut_photo" alt="" class="avatar" />
          </template>
          <template #default>
            <div>
              <van-button type="info" size="mini" v-if="articleDetailList.is_followed === true" @click="attention(true)">已关注</van-button>
              <van-button icon="plus" type="info" size="mini" plain v-else @click="attention(false)">关注</van-button>
            </div>
          </template>
        </van-cell>

        <!-- 分割线 -->
        <van-divider></van-divider>

        <!-- 文章内容 -->
        <div class="art-content" v-html="articleDetailList.content"></div>

        <!-- 分割线 -->
        <van-divider>End</van-divider>

        <!-- 点赞评论 -->
        <div class="like-box">
          <van-button icon="good-job" type="danger" size="small" v-if="articleDetailList.attitude === 1" @click="loveFn(true)">已点赞</van-button>
          <van-button icon="good-job-o" type="danger" plain size="small" v-else-if="articleDetailList.attitude === -1" @click="loveFn(false)">点赞</van-button>
        </div>
      </div>
      <!-- 文章评论 -->
      <comment></comment>
    </div>
  </div>
</template>

<script>
import { getArticleDetailAPI, attentionAuthor, unAttentionAuthor, loveAPI, unLoveAPI } from '@/api/api.js'
import Comment from '@/components/home/Comment.vue'
export default {
  name: 'ArticleDetail',
  data() {
    return {
      // 这是个对象，不是数组
      articleDetailList: {}
    }
  },
  async created() {
    // 获取文章详情
    // 文章id从哪里来？从查询哈希字符串的方式
    const res = await getArticleDetailAPI({
      article_id: this.$route.query.art_id
    })
    // console.log(res)
    this.articleDetailList = res.data.data
  },
  methods: {
    // 关注和取关作者的函数
    async attention(bool) {
      if (bool === true) {
        this.articleDetailList.is_followed = false
        // 取关接口
        await unAttentionAuthor({
          aut_id: this.articleDetailList.aut_id
        })
        // console.log(res)
      } else {
        this.articleDetailList.is_followed = true
        // 关注接口
        await attentionAuthor({
          aut_id: this.articleDetailList.aut_id
        })
        // console.log(res1)
      }
    },
    // 点赞，取消点赞
    async loveFn(bool) {
      if (bool === true) {
        // 取消点赞
        this.articleDetailList.attitude = -1
        const res1 = await unLoveAPI({
          art_id: this.$route.query.art_id
        })
        console.log(res1)
      } else {
        // 点赞
        this.articleDetailList.attitude = 1
        const res2 = await loveAPI({
          art_id: this.$route.query.art_id
        })
        console.log(res2)
      }
    }
  },
  components: {
    Comment
  }
}
</script>

<style lang="less" scoped>
.article-container {
  padding: 56px 10px 10px 10px;
  // margin-top: 46px;
}
.art-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;
  /deep/ img {
    width: 100%;
  }
  /deep/ pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}

.van-cell {
  padding: 5px 0;
  &::after {
    display: none;
  }
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f8f8f8;
  margin-right: 5px;
  border: none;
}

.like-box {
  display: flex;
  justify-content: center;
}
.loading {
  text-align: center;
  padding-top: 46px;
}
</style>
