<template>
  <div>
    <!-- 评论列表 -->
    <!-- 动态class用来根据不同的情况切换不同的类名，格式别忘了 -->
    <!-- 因为只有两种状态，所以只需要两种类名来切换即可 -->
    <div class="cmt-list" :class="{ 'cmt-container-1': !isShowCommentInputVisible, 'cmt-container-2': isShowCommentInputVisible }">
      <!-- 这个list组件是用来包裹每一项的 -->
      <van-list v-model="loading" :finished="finished" finished-text="没有更多评论辣，快去发表评论吧" @load="onLoad" :immediate-check="false">
        <!-- 评论的 Item 项 -->
        <div class="cmt-item" v-for="item in commentList" :key="item.com_id">
          <!-- 头部区域 -->
          <div class="cmt-header">
            <!-- 头部左侧 -->
            <div class="cmt-header-left">
              <img :src="item.aut_photo" alt="" class="avatar" />
              <span class="uname">{{ item.aut_name }}</span>
            </div>
            <!-- 头部右侧 -->
            <div class="cmt-header-right">
              <van-icon name="like" size="16" color="red" v-if="item.is_liking === true" @click="likeComment(true, item)" />
              <van-icon name="like-o" size="16" color="gray" v-else @click="likeComment(false, item)" />
            </div>
          </div>
          <!-- 主体区域 -->
          <div class="cmt-body">{{ item.content }}</div>
          <!-- 尾部区域 -->
          <div class="cmt-footer">{{ item.pubdate }}</div>
        </div>
      </van-list>
    </div>

    <!-- 底部添加评论区域 - 1 -->
    <div class="add-cmt-box van-hairline--top" v-if="isShowCommentInputVisible === false">
      <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
      <div class="ipt-cmt-div" @click="isShowCommentInputVisible = true">发表评论</div>
      <div class="icon-box">
        <!-- 需求:1 评论总数为0的时候不显示图标;  2 评论总数大于99时显示99+ -->
        <van-badge :content="totalComment === 0 ? '' : totalComment" max="99">
          <van-icon name="comment-o" size="0.53333334rem" @click="scrollToComment" />
        </van-badge>
        <!-- 收藏文章 -->
        <span>
          <van-icon name="star-o" size="0.53333334rem" v-if="isStar === false" @click="starFn" />
          <van-icon name="star" size="0.53333334rem" v-else color="red" @click="starFn" />
        </span>
        <van-icon name="share-o" size="0.53333334rem" @click="showShareFn" />
      </div>
    </div>
    <!-- 底部添加评论区域 - 2 -->
    <div class="cmt-box van-hairline--top" v-else>
      <textarea placeholder="请发表一个友善的评论哟" v-fofo @blur="blurFn" v-model.trim="commentContent"></textarea>
      <van-button type="default" :disabled="commentContent.length === 0" @click="sendCommentFn">发布</van-button>
    </div>

    <!-- 分享组件 -->
    <van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" />
  </div>
</template>

<script>
import { getCommentListAPI, likeCommentAPI, unlikeCommentAPI, sendCommentAPI, collectArticleAPI } from '@/api/api.js'
export default {
  data() {
    return {
      // 获取到的评论列表，接下来去循环列表数据去铺设数据
      commentList: [],
      // 控制评论分页偏移量的
      lastId: null,
      // 控制发布评论的切换
      isShowCommentInputVisible: false,
      // 获取到的评论总数
      totalComment: 0,
      // 输入的评论内容
      commentContent: '',
      // 控制加载状态的
      loading: false,
      // 监控到没有list数据了就会转为true
      finished: false,
      // 控制是否对文章表示喜欢
      isStar: false,
      // 控制分享面板的显示与隐藏
      showShare: false,
      // 分享到其他平台
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' }
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' }
        ]
      ]
    }
  },
  async created() {
    // 一创建就获取评论数据
    const res = await getCommentListAPI({
      // 通过查询字符串的方式获取到文章的id
      art_id: this.$route.query.art_id
    })
    // console.log(res)
    this.commentList = res.data.data.results
    this.totalComment = res.data.data.total_count
    this.lastId = res.data.data.last_id
    // 需求：在刚创建评论列表的时候，如果没有评论数据，就显示没有更多评论了
    if (this.totalComment === 0) {
      this.finished = true
    }
  },
  methods: {
    // 评论的点赞与取消点赞
    // 因为在data里面没有item，所以必须要在点击事件里面手动传递一个item进来
    async likeComment(bool, item) {
      if (bool === true) {
        // 取消点赞
        item.is_liking = false
        await unlikeCommentAPI({
          com_id: item.com_id
        })
      } else {
        //  点赞
        item.is_liking = true
        await likeCommentAPI({
          com_id: item.com_id
        })
      }
    },
    // 点击评论的图标，自动滚动到评论列表
    scrollToComment() {
      // 总结：动画有三种方法：
      // 1 css的动画或者过渡  2 原生js利用定时器来做  3利用 原生标签.scrollIntoview()

      // 因为js代码是在html和css创建完成之后才会执行，所以通过document向下获取类名是可以的，不一定非要在同一个组件内才能获取到
      // const articleHeight = document.querySelector('.article-container').scrollHeight
      // console.log(articleHeight)
      // window.scrollTo(0, articleHeight - 46)

      // 比较方便的方法(既能滚动也能带动画)
      // 原生标签.scrollIntoview()，让原生的标签滚动到屏幕的最上面l/为何选择like-box不选择第一条评论，因为头部导航会挡住
      // 注意:有的人的电脑不支持这个方法，没有滑动的效果-〉只能用原生Js写(兼容性好)
      document.querySelector('.like-box').scrollIntoView({
        behavior: 'smooth'
      })
    },
    // 点击发布按钮，添加评论
    async sendCommentFn() {
      // console.log(this.commentContent)
      // 问题：这里点击发布，打印不出来数据
      // 原因：高的评论容器-在页面点击发布一瞬间，输入框失去了焦点，被v-if和v-else移除了整个标签导致点击事件没来得及执行
      // 解决：失去焦点时,变量值，在最后再执行,即把失去焦点这个事件通过一个定时器把它变成一个异步事件，就可以延迟销毁
      const res = await sendCommentAPI({
        id: this.$route.query.art_id,
        content: this.commentContent
      })
      // 调用发布评论的接口后会返回一个新对象，这个对象里就有刚刚新增的数据结构
      // console.log(res)
      // 把新增的这个评论数据unshift到评论列表的最前面，不能用push，要用unshift
      this.commentList.unshift(res.data.data.new_obj)
      // 因为我们的新增评论是追加上去的，不是刷新得到的，所以在新增评论后，评论总数不会显示变化，所以需要手动添加
      this.totalComment++
      // 需求：一发表新的评论后，就自动滚动到新增的评论处
      this.scrollToComment()
      // 清空textarea绑定的数据
      this.commentContent = ''
    },
    // 失去焦点时切换评论容器
    blurFn() {
      setTimeout(() => {
        this.isShowCommentInputVisible = false
      }, 0)
    },
    // 上拉加载时默认触发的事件
    // list组件默认一开始就会触发load事件，所以不要让他一开始就触发事件，需要在组件标签上添加属性immediate-check,其实加了这个属性就不需要判断this.commentList.length > 0了
    async onLoad() {
      // 判断的原因就是不希望一初始化就立即执行滚动检查
      if (this.commentList.length > 0) {
        const res = await getCommentListAPI({
          art_id: this.$route.query.art_id,
          offset: this.lastId
        })
        console.log(res)
        this.commentList = [...this.commentList, ...res.data.data.results]
        this.totalComment = res.data.data.total_count
        this.lastId = res.data.data.last_id
        if (this.lastId === null) {
          this.finished = true
        }
        this.loading = false
      } else {
        this.loading = false
      }
    },
    // 控制分享面板的显示与隐藏
    showShareFn() {
      this.showShare = true
    },
    // 点击喜欢，图标变红
    async starFn() {
      // console.log(111)
      this.isStar = !this.isStar
      const res = await collectArticleAPI({
        art_id: this.$route.query.art_id
      })
      console.log(res)
    }
  }
}
</script>

<style lang="less" scoped>
.cmt-list {
  padding: 10px;
  .cmt-item {
    padding: 15px 0;
    + .cmt-item {
      border-top: 1px solid #f8f8f8;
    }
    .cmt-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .cmt-header-left {
        display: flex;
        align-items: center;
        .avatar {
          width: 40px;
          height: 40px;
          background-color: #f8f8f8;
          border-radius: 50%;
          margin-right: 15px;
        }
        .uname {
          font-size: 12px;
        }
      }
    }
    .cmt-body {
      font-size: 14px;
      line-height: 28px;
      text-indent: 2em;
      margin-top: 6px;
      word-break: break-all;
    }
    .cmt-footer {
      font-size: 12px;
      color: gray;
      margin-top: 10px;
    }
  }
}
// 控制评论底部距离发布评论的距离
.cmt-container-1 {
  padding-bottom: 40px;
}
.cmt-container-2 {
  padding-bottom: 80px;
}

/*美化 - 文章详情 - 底部的评论页面 */
// 外层容器
.art-cmt-container-1 {
  padding-bottom: 46px;
}
.art-cmt-container-2 {
  padding-bottom: 80px;
}
// 对发布评论的组件进行美化
// 发布评论的盒子 - 1
.add-cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 46px;
  line-height: 46px;
  padding-left: 10px;
  .ipt-cmt-div {
    flex: 1;
    border: 1px solid #efefef;
    border-radius: 15px;
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    padding-left: 15px;
    margin-left: 10px;
    background-color: #f8f8f8;
  }
  .icon-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    line-height: 0;
  }
}

.child {
  width: 20px;
  height: 20px;
  background: #f2f3f5;
}

// 发布评论的盒子 - 2
.cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: white;
  textarea {
    flex: 1;
    height: 66%;
    border: 1px solid #efefef;
    background-color: #f8f8f8;
    resize: none;
    border-radius: 6px;
    padding: 5px;
  }
  .van-button {
    height: 100%;
    border: none;
  }
}
</style>
